import "./styles.css";
import bucketlist from "./bucketlist";
import MyCard from "./card";

function menus(index) {
  const items = [];
  const keys = Object.keys(bucketlist[index]);
  const values = Object.values(bucketlist[index]);
  for (let i = 0; i < values.length; i++) {
    if (keys[i] === "写真") {
      items.push(<img src={values[i]} alt="画像の説明"></img>);
    } else {
      items.push(
        <li>
          {keys[i]}:{values[i]}
        </li>
      );
    }
  }
  return <ul>{items}</ul>;
}

export default function App() {
  return (
    <>
      <h1>Bucketlist</h1>
      <MyCard imgPath="https://former-cdn.cinra.net/uploads/img/report/201809-eve_l_full.jpg" />
      {menus(0)}
      {menus(1)}
      {menus(2)}
      {menus(3)}
      {menus(4)}
      {menus(5)}
      {menus(6)}
    </>
  );
}
